import moment from "moment";

import { mapState } from "vuex";
// Import component
import Loading from "vue-loading-overlay";
// Import stylesheet
import "vue-loading-overlay/dist/vue-loading.css";

export default {
  data: () => ({
    station: null,
    isLoading: false,
    fullPage: true,
    today: moment().format("YYYY-MM-DD"),
    availableDates: [],
    disabled: [],
    days: [],
    selectedDate: [],
    date: new Date(),
    timezone: "Asia/Kuala_Lumpur",
    range: {
      start: null,
      end: null,
    },
    start: null,
    end: null,
  }),
  computed: {
    // dates() {
    //   return this.days.map(day => day.date);
    // },
    // attributes() {
    //   return this.dates.map(date => ({
    //     highlight: true,
    //     dates: date
    //   }));
    // }
  },
  computed: {
    ...mapState({
      stations: (state) => state.stations,
    }),
  },
  components: {
    Loading,
  },
  methods: {
    download: function (station, start, end) {
      // this.setLoading1(true);
      console.log(start);
      if (start == null || end == null) {
        alert("Please select valid date/s");
        return;
      }
      if (station == null) {
        alert("Please select jetty");
        return;
      }
      this.isLoading = true;
      this.$axios

        .$get(`http://167.99.75.232:7777/api/download`, {
          // .$get(`http://localhost:5000/api/download`, {
          params: {
            // .$get(`/api/daily/`,{params:{
            station: station,
            start: start,
            end: end,
          },
        })
        .then((response) => {
          console.log(response);
          // res.download(response);
          if (typeof response !== "object") {
            let blob = new Blob([response], { type: "text/csv" });
            let link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.download = "Report.csv";
            link.click();
            this.isLoading = false;
          } else {
            alert(response.msg);
            this.isLoading = false;
          }
        })
        .catch((error) => {
          console.log(error);
          alert(error);
          this.isLoading = false;
        });
    },
    clear: function () {
      for (let i = 0; i <= 4; i++) {
        // if (i != index) {
        let deactive = document.getElementById(i).classList.remove("active");
        // }
      }
    },
    console: function (index) {
      // console.log(index);
      const active = document.getElementById(index).classList.add("active");
      for (let i = 0; i <= 4; i++) {
        if (i != index) {
          let deactive = document.getElementById(i).classList.remove("active");
        }
      }
      let station = [
        "geyser_one",
        "geyser_two",
        "geyser_four",
        "geyser_three",
        "geyser_five",
      ];
      let sensorArray = [];
      this.station = station[index];
      // console.log(this.station);
    },
    getDatesBetweenDates: function (startDate, endDate) {
      let dates = [];
      //to avoid modifying the original date
      const theDate = new Date(startDate);
      // console.log(endDate);
      while (theDate < endDate) {
        dates = [...dates, moment(new Date(theDate)).format("YYYY-MM-DD")];
        theDate.setDate(theDate.getDate() + 1);
      }
      dates = [...dates, moment(endDate).format("YYYY-MM-DD")];
      return dates;
    },
  },
  watch: {
    range: function (val) {
      // console.log(val);
      if (val != null) {
        this.start = moment(this.range.start).format("YYYY-MM-DD");
        this.end = moment(this.range.end).format("YYYY-MM-DD");
        var dateList = this.getDatesBetweenDates(
          this.range.start,
          this.range.end
        );
      }
      // this.reset = false;
      // console.log(dateList);
      // console.log(this.start, this.end);
    },
  },
  async mounted() {
    // this.getScheduleIpah1();
  },
};
